.carte {
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid black;
    border-radius:  8px;
    overflow: hidden;
    width: 100%;
    max-width: 1800px;
    height: 800px;
    margin: 20px auto; 
    background-color: rgba(255,255,255,.7);
    gap: 2rem;
   
  }
  
  .carte-image {
    /* width: 400px; */
    max-width: 30%;
  }
  
  .carte-info {
    display: flex;
    padding: 20px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
    flex-direction: column;
    align-items: flex-start; 
    /* background-color: aqua; */
  }
  
  .carte h2 {
    margin-bottom: 10px;
    color: var(--primary);
    font-size: 2rem;
    font-family: 'Arial', sans-serif;
  }
  
  .carte p {
    margin-bottom: 8px;
    font-size: 1.4rem;
    font-family: 'Arial', sans-serif;
  }
  
  .carte .btn {
    text-decoration: none;
    background-color: #4caf50;
    color: white;
    padding: 10px 20px;
    border-radius: 5px;
    display: inline-block;
    margin-top: auto; 
    margin-right:10px;
    margin-bottom: 10px;
  }
  
  .carte .btn:hover {
   opacity: 0.7;
  }

  h1 {
    font-family: 'Lobster', cursive;
    margin: 50px 20px 40px;
    font-size: 50px;
    letter-spacing: 0.5px;
    color: #999999;
    text-align: center;
  }

 .carte-commentaire > p {
    
   font-size: 1.8rem;
  

  }

  .film-vote-commentaire{
    display: flex;
    /* background-color: #ff5733; */
    flex-basis: 100%;
  }
  
  .commentaire-film{
    flex-basis: 50%;
  }

  .moyenne-vote{

    flex-basis: 50%;

  }